import { Button, Stack, Typography } from "@mui/material";
import Attribute from "./Attribute";
import { usePersistenceContext } from "./PersistenceContext"
import { useRollerContext } from "./DiceContext";

export default function Attributes() {
  const {str, setStr, dex, setDex, con, setCon, int, setInt, wis, setWis, cha, setCha} = usePersistenceContext()
  const rollerCtx = useRollerContext()

  const showReset = str && Number(str) < 14 && dex && Number(dex) < 14 && con && Number(con) < 14 && int && Number(int) < 14 && wis && Number(wis) < 14 
  return (
    <div style={{ flex: 1, border: '1px solid darkGray', padding: 10 }}>
      <Stack direction="row" style={{ display: 'flex', justifyContent: 'space-between' }}>
        <Typography variant="h6">Attributes</Typography>
        { showReset && (
          <Button color="primary" size="small" onClick={() => {
            setStr('')
            setDex('')
            setCon('')
            setInt('')
            setWis('')
            setCha('')
            rollerCtx.clearDice()
          }}>
            No stat is over 13 - you can start over
          </Button>
        )}
      </Stack>

      <Stack direction="row" style={{ display: 'flex', flex: 1, justifyContent: 'space-between', marginTop: 5, }}>
        <Attribute name="Str" value={str} onChange={(v) => setStr(v)} description="Physical might and stregth. Important for Fighter"/>
        <Attribute name="Dex" value={dex} onChange={(v) => setDex(v)} description="Agility and reflexes. Important for Thief"/>
        <Attribute name="Con" value={con} onChange={(v) => setCon(v)} description="Physical robustness. Improves health for any character"/>
        <Attribute name="Int" value={int} onChange={(v) => setInt(v)} description="Aptitude for learning and mysteries. Important for Wizards"/>
        <Attribute name="Wis" value={wis} onChange={(v) => setWis(v)} description="Judge carefully, detect the hidden, recognize omens. Important for priests."/>
        <Attribute name="Cha" value={cha} onChange={(v) => setCha(v)} description="Persuade, charm, or coerce others"/>
      </Stack>
    </div>
  )
}
