import React, { useEffect, useState } from 'react'
import { Grid, Typography } from '@mui/material'
import { useInventoryContext } from '../InventoryContext'
import { usePersistenceContext } from '../PersistenceContext'
import ItemSlot from './ItemSlot'

export default function Inventory() {
  const { inventory } = useInventoryContext()
  const { str } = usePersistenceContext()

  const [totalWeight, setTotalWeight] = useState(0)
  const maxWeight = Math.max(10, Number(str || '10'))

  useEffect(() => {
    setTotalWeight(inventory.reduce((acc, item) => acc + (item.label ? item.weight : 0), 0))
  }, [inventory])

  return (
    <div style={{ flex: 1, border: '1px solid darkGray', marginTop: 20, padding: 10 }}>
      <Typography variant="h6">Gear</Typography>

      {totalWeight == 0 && (
        <Typography variant="caption">New characters start with a coinbag containing 2d6 x 5 Gold Pieces</Typography>
      )}

      <Grid container style={{ border: '0px solid blue'}}>
        {Array(maxWeight + 2).fill('haha').map((item, idx) => (
          <ItemSlot key={idx}
                    idx={idx + 1}
          />
        ))}
      </Grid>

      <Typography variant="body1" style={{ marginTop: 8, marginLeft: 8 }}>Weight: {totalWeight} / {maxWeight}</Typography>

      {totalWeight > maxWeight && (
        <Typography variant="caption" style={{ color: 'red' }}>OVERLOADED</Typography>
      )}
    </div>
  )
}
