import { Box, Button, TextField, Tooltip, Typography } from "@mui/material"
import { useRollerContext } from "./DiceContext"

export function getBonus(value: number | string) {
  return Math.max(-4, Math.min(4, Math.floor(Number(value) / 2) - 5))
}
export default function Attribute({ name, description, value, onChange }: { name: string, description: string, value: string | null, onChange: (value: string) => void }) {
  const handleChange = (ev: { target: { value: any; }; }) => {
    if (Number(ev.target.value) >= 0) {
      onChange(ev.target.value)
    }
  }

  const rollerCtx = useRollerContext()

  const rollAttribute = () => {
    rollerCtx.clearDice()
    rollerCtx.rollDice("3d6", (result: number) => {
      onChange("" + result)
    })
  }
  
  return (
    <div style={{
      display: 'flex',
      paddingLeft: 10,
      paddingRight: 10,
      alignItems: 'center',
      flexDirection: 'column',
    }}>
      <Tooltip title={description}>
        <Typography variant="body1">{name}</Typography>
      </Tooltip>

      <Box width={60} sx={{ mb: 1 }}>
        <TextField 
          type="tel" 
          value={value || ""}
          size="small" 
          inputProps={{min: 0, style: { textAlign: 'center' }}}
          onChange={handleChange}
        />
      </Box>

      { (!value || Number(value) === 0) && (
        <Button variant="outlined" color="primary" size="small" onClick={rollAttribute}>
          Roll
        </Button>
      )}

      { value && Number(value) > 0 ? (
        <span>{getBonus(value) > 0 ? "+" : ""}{getBonus(value)}</span>
      ) : <span>&nbsp;</span> }
    </div>
  )
}
