import React, { useState } from 'react'
import { Box, TextField, Typography } from '@mui/material'
import { usePersistenceContext } from './PersistenceContext'

export default function Experience() {
  const { xp, setXp } = usePersistenceContext()

  const handleXp = (ev: React.ChangeEvent<HTMLInputElement>) => {
    const newVal = ev.target.value
    if (newVal === '' || Number.isInteger(Number(newVal))) {
      setXp(ev.target.value)
    } else {
      console.log('Invalid value: ', newVal)
    }
  }
  
  return (
    <div style={{
      display: 'flex',
      border: '1px solid darkGray',
      padding: 10,
      alignItems: 'center',
      flexDirection: 'column',
      borderRadius: 4,
    }}>
      <Typography variant="h6">Experience</Typography>

      <Box width={60} sx={{ mt: 2 }}>
        <TextField value={xp || ''} size="small" onChange={handleXp}/>
      </Box>
    </div>
  )
}
