import React, { useEffect, useState } from 'react'
import { Button, Dialog, DialogContent, DialogTitle, Stack, Table, TableBody, TableCell, TableHead, TableRow, TextField, Typography } from '@mui/material'
import { RandomName } from './RandomName'
import { usePersistenceContext } from './PersistenceContext'
import { useInventoryContext } from './InventoryContext'

type CharacterStub = {
  id: string
  name: string
  ancestry: string
  heroPath: string
}

export default function CharacterPicker({ email, setEmail, name, setName, onSelect }: { email: string, setEmail: (email: string) => void, name: string, setName: (name: string) => void, onSelect: (id: string) => void }) {
  const [loading, setLoading] = useState(!!email)
  const [characters, setCharacters] = useState<CharacterStub[]>([])
  const [deletingId, setDeletingId] = useState<string | null>(null)

  const { supabase } = usePersistenceContext()
  const { upsertItem } = useInventoryContext()

  const loadCharacterList = async () => {
    console.log('Loading character list for ' + email)
    if (email !== '') {
      setLoading(true)

      const { data, error } = await supabase
        .from("Character")
        .select()
        .range(0, 49)
        .eq("player_email", email)
        .order("id", { ascending: false });
      console.log(`data`, data);

      if (data) {
        setCharacters(data.map((c: any) => ({ id: c.id, name: c.name || 'Unnamed', ancestry: c.ancestry, heroPath: c.heroPath })))
      }
      if (error) {
        console.error('Error loading characters', error)
      }

      setLoading(false)
    }
  }

  useEffect(() => {
    setCharacters([])
    const timerId = setTimeout(loadCharacterList, 1500)
    return () => clearTimeout(timerId)
  }, [email, supabase])

  const handleEmailChange = (ev: React.ChangeEvent<HTMLInputElement>) => {
    setEmail(ev.target.value)
  }

  const handleNameChange = (ev: React.ChangeEvent<HTMLInputElement>) => {
    setName(ev.target.value)
  }

  const newCharacter = async () => {
    const { data, error } = await supabase
      .from("Character")
      .insert({ player_email: email, name: RandomName() })
      .select()
      console.log(`new character data`, data);

    if (data) {
      onSelect(data[0].id)
    }
    if (error) {
      console.error('Error creating character', error)
    }
  }

  const startDelete = (id: string) => {
    setDeletingId(id)
  }

  const confirmDelete = async () => {
    if (deletingId) {
      console.log('Deleting character ' + deletingId)
      await supabase
        .from("Character")
        .delete()
        .eq("id", deletingId)

      setDeletingId(null)

      setCharacters([])
      setLoading(true)
      setTimeout(loadCharacterList, 2500)
    }
  }

  return (
    <Stack style={{display: 'flex', marginTop: 10, marginBottom: 50, flexDirection: 'column', width: 680 }}>
      <Typography variant='h6' sx={{ mb: 3 }}>Character Picker</Typography>

      <Stack direction="row" spacing={2} sx={{ alignItems: 'center' }}>
        <TextField label="Name" type="email" value={name} onChange={handleNameChange} />
        <TextField label="Email" type="email" value={email} onChange={handleEmailChange} />
        { loading && <Typography>Loading…</Typography> }
      </Stack>

      <Stack direction="column" spacing={2} sx={{ mt: 2 }}>
        { characters.length > 0 && (
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  <Typography variant="h6">Name</Typography>
                </TableCell>
                <TableCell>
                  <Typography variant="h6">Ancestry</Typography>
                </TableCell>
                <TableCell>
                  <Typography variant="h6">Class</Typography>
                </TableCell>
                <TableCell>
                  <Typography variant="h6">Actions</Typography>
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              { characters.map(c => (
                <TableRow key={c.id}>
                  <TableCell>{c.name || 'Unnamed'}</TableCell>
                  <TableCell>{c.ancestry || '?'}</TableCell>
                  <TableCell>{c.heroPath || '?'}</TableCell>
                  <TableCell>
                    <Button variant="outlined" onClick={() => onSelect(c.id)}>Load</Button>
                    <Button sx={{ ml: 2 }} onClick={() => startDelete(c.id)}>Delete</Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        )}

        { characters.length === 0 && !loading && (
          <Typography>No characters found</Typography>
        )}

        { email && !loading && (
          <Button variant="outlined" onClick={newCharacter}>New Character</Button>
        )}

        { deletingId && (
          <Dialog open={true} onClose={() => setDeletingId(null)}>
            <DialogTitle>Delete Character?</DialogTitle>
            <DialogContent>
              <Stack direction="row" spacing={2}>
                <Button onClick={confirmDelete}>Yes</Button>
                <Button onClick={() => setDeletingId(null)}>No</Button>
              </Stack>
            </DialogContent>
          </Dialog>
        )}
      </Stack>
    </Stack>
  )
}
