import { Box, Button, Stack, Table, TableBody, TableCell, TableRow, TextField, Typography } from "@mui/material";
import { FIGHTER, PRIEST } from "./HeroPath";
import { useRollerContext } from "./DiceContext";
import { useState } from "react";
import { usePersistenceContext } from "./PersistenceContext";
import { useFeatsContext } from "./FeatsContext";
import { HUMAN } from "./Ancestry";

export type ClassFeatType = {
  heroPath: string
  fromDie: number
  toDie: number
  description: string
  hasNote: boolean
  noteLabel?: string
}

const CLASS_FEATS: ClassFeatType[] = [
  { heroPath: FIGHTER, fromDie: 2, toDie: 2, description: 'Gain Weapon Mastery with one additional weapon type', hasNote: false, noteLabel: 'Weapon type' },
  { heroPath: FIGHTER, fromDie: 3, toDie: 6, description: '+1 to melee and ranged attacks', hasNote: false, noteLabel: 'Melee or ranged' },
  { heroPath: FIGHTER, fromDie: 7, toDie: 9, description: '+2 to Strength, Dexterity, or Constitution stat', hasNote: false, noteLabel: 'Stat' },
  { heroPath: FIGHTER, fromDie: 10, toDie: 11, description: 'Choose one kind of armor. You get +1 AC from that armor', hasNote: true, noteLabel: 'Armor type' },
  { heroPath: FIGHTER, fromDie: 12, toDie: 12, description: 'Choose a talent or +2 points to distribute to stats', hasNote: true, noteLabel: 'Bonus' },

  { heroPath: PRIEST, fromDie: 2, toDie: 2, description: 'Gain advantage on casting one spell you know', hasNote: true, noteLabel: 'Spell' },
  { heroPath: PRIEST, fromDie: 3, toDie: 6, description: '+1 to melee or ranged attacks', hasNote: true, noteLabel: 'Melee or ranged' },
  { heroPath: PRIEST, fromDie: 7, toDie: 9, description: '+1 to priest spellcasting checks', hasNote: false },
  { heroPath: PRIEST, fromDie: 10, toDie: 11, description: '+2 to Strength or Wisdom stat', hasNote: true, noteLabel: 'Stat' },
  { heroPath: PRIEST, fromDie: 12, toDie: 12, description: 'Choose a talent or +2 points to distribute to stats', hasNote: true, noteLabel: 'Bonus' },
]

export default function Feats() {
  const { ancestry, heroPath } = usePersistenceContext()
  const { feats, addFeat } = useFeatsContext()

  const rollerCtx = useRollerContext()

  const [showFeats, setShowFeats] = useState(false)

  const rollForFeat = () => {
    rollerCtx.clearDice()
    rollerCtx.rollDice("2d6", (result: number) => {
      console.log('Just rolled a ' + result)
      const feat = CLASS_FEATS.find(f => f.heroPath === heroPath && f.fromDie && f.toDie && f.fromDie <= result && f.toDie >= result)
      console.log('feat', feat)
    })
  }

  const border = showFeats ? '1px solid darkGray' : 'none'
  return (
    <div style={{ flex: 1, border, padding: 10, marginTop: 20 }}>
      <Button variant="outlined" color="primary" onClick={() => setShowFeats(!showFeats)}>
        { showFeats ? 'Hide' : 'Show' } Feats
      </Button>

      {
        showFeats && (
          <Button variant="outlined" color="primary" style={{ marginLeft: 15 }} onClick={rollForFeat}>
            Roll for Feat
          </Button>
      )}

      { feats.length === 0 && (
        <Typography variant="caption" component='span' sx={{ ml: 2 }}>
          { ancestry === HUMAN ? (
              <>At first level, humans start with two feats.</>
            ) : (
              <>You get one feat at first level.</>
            )
          }
        </Typography>
      )}

      {
        showFeats && (
          <div>
            <Typography variant="h6" style={{ marginTop: 10 }}>
              {heroPath} Feats
            </Typography>
            <Table>
              <TableBody>
                { CLASS_FEATS.filter(f => f.heroPath === heroPath).map(f => (
                  <TableRow key={f.description}>
                    <TableCell style={{ padding: 10 }} width={50} align="right">{f.fromDie} - {f.toDie}</TableCell>
                    <TableCell style={{ padding: 10 }} width={300}>{f.description}</TableCell>
                    <TableCell style={{ padding: 10 }} width={50}>
                      <Button variant="outlined" 
                              color="primary" 
                              size="small"
                              onClick={() => addFeat(f)}
                      >
                        Add
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        )
      }
    </div>
  )
}
