import React, { useEffect, useState } from 'react'
import { usePersistenceContext } from './PersistenceContext'
import { Button, Checkbox, duration, FormControlLabel, IconButton, Stack, Typography } from '@mui/material'
import { Delete } from '@mui/icons-material'
import SpellList from './SpellList'
import { Spell } from './Grimoire'
import { PRIEST } from './HeroPath'

type SpellSlot = {
  dbId?: string
  characterId: string
  spell: string
  duration: string
  note: string
  level: number
  ready: boolean
}

function SpellSlotWidget({ slot, onToggleReady, onDelete }: { slot: SpellSlot, onToggleReady: (dbId: string) => void, onDelete: (dbId: string) => void }) {
  return (
    <Stack sx={{ alignItems: 'center', mb: 2 }} direction="row">
      <Stack sx={{ flex: 6 }} direction="column">
        <Stack direction="row">
          <strong>{slot.spell}</strong>
          <Typography sx={{ ml: 1 }} variant='caption'>(Tier {slot.level})</Typography>
        </Stack>
        <Typography variant='caption'>Duration: {slot.duration}</Typography>
        { slot.note && (
          <p style={{ flex: 3, padding: 0, marginTop: 10, marginLeft: 15, marginBottom: 5 }}>
            {slot.note}
          </p>
        )}
      </Stack>
      <FormControlLabel sx={{ flex: 1, ml: 2 }} control={
        <Checkbox checked={slot.ready} 
                  onChange={() => onToggleReady(slot.dbId || '')} 
        />} 
        label={<Typography variant='caption'>Ready</Typography>} 
      />
      <IconButton sx={{ color: "#aaa", flex: 1,  }} onClick={() => onDelete(slot.dbId || '')}>
        <Delete />
      </IconButton>
    </Stack>
  )
}

export default function Spells() {
  const { supabase, id: characterId, heroPath } = usePersistenceContext()
  const [spellSlots, setSpellSlots] = useState<SpellSlot[]>([])
  const [showAddSpell, setShowAddSpell] = useState(false)

  const loadSpells = async () => {
    if (!characterId) {
      console.log('No character ID, not loading spells')
      return
    }

    const { data, error } = await supabase.from('SpellSlot').select().eq('characterId', characterId)
    if (data) {
      const newSlots = data.map((i: any) => ({ 
        dbId: i.id, 
        characterId: i.characterId, 
        spell: i.spell, 
        note: i.note,
        duration: i.duration,
        level: i.level,
        ready: i.ready,
      }))
      const slotComparator = (a: SpellSlot, b: SpellSlot) => {
        if (a.level === b.level) {
          return a.spell.localeCompare(b.spell)
        }
        return a.level - b.level
      }
      const sortedSlots = newSlots.sort(slotComparator)
      setSpellSlots(sortedSlots)
    }
  }

  const handleAddSpell = async (spell: Spell) => {
    const newSlot: SpellSlot = {
      characterId,
      spell: spell.name,
      note: spell.description,
      duration: spell.duration,
      level: spell.tier,
      ready: true,
    }
    const { data, error } = await supabase.from('SpellSlot').insert(newSlot).select()
    loadSpells()
  }

  const toggleReady = async (dbId: string) => {
    const slot = spellSlots.find(s => s.dbId === dbId)
    if (slot) {
      const newSlot = { ...slot, ready: !slot.ready }
      const { data, error } = await supabase.from('SpellSlot').update({ ready: !slot.ready }).eq('id', dbId).select()
      console.log('Data: ', data)
      if (error) {
        console.error('Error updating spell slot', error)
      }
      setSpellSlots(spellSlots.map(s => s.dbId === dbId ? newSlot : s))
    }
  }

  const handleDeleteSpellByName = async (spellName: string) => {
    console.log('Deleting spell ' + spellName)
    setSpellSlots(spellSlots.filter(s => s.spell !== spellName))
    const {data, error} = await supabase.from('SpellSlot').delete().eq('spell', spellName).eq('characterId', characterId)
    if (error) {
      console.error('Error deleting spell slot', error)
    }
    loadSpells()
  }

  const handleDeleteSpell = async (dbId: string) => {
    console.log('Deleting spell slot ' + dbId)
    setSpellSlots(spellSlots.filter(s => s.dbId !== dbId))
    const {data, error} = await supabase.from('SpellSlot').delete().eq('id', dbId)
    if (error) {
      console.error('Error deleting spell slot', error)
    }
    loadSpells()
  }

  useEffect(() => {
    loadSpells()
  }, [characterId, supabase, loadSpells])

  return (
    <Stack style={{ flex: 1, border: '1px solid darkGray', padding: 10, marginTop: 20 }}>
      <Typography variant="h6" component='span' sx={{ mb: 2 }}>
        Spells
      </Typography>

      { heroPath === PRIEST && (
        <Typography variant="caption" component='span' sx={{ mb: 2 }}>
          At 1st level, you know Turn Undead + 2 spells of your choice.
        </Typography>
      )}

      { spellSlots.map((slot) => (
          <SpellSlotWidget key={slot.dbId} 
                           slot={slot}
                           onToggleReady={toggleReady}
                           onDelete={handleDeleteSpell}
          />
        )) 
      }

      { showAddSpell ? (
        <SpellList handleClose={() => setShowAddSpell(false)} 
                   onAddSpell={handleAddSpell} 
                   onDeleteSpell={handleDeleteSpellByName}
                   selectedSpellNames={spellSlots.map(s => s.spell)}
        />
      ) : (
        <div style={{ marginTop: 10 }}>
          <Button variant='outlined' onClick={() => setShowAddSpell(true)}>Add Spell</Button>
        </div>
      )}
    </Stack>
  )
}
