import React from 'react'
import { usePersistenceContext } from './PersistenceContext'
import { Button, Stack, TextField } from '@mui/material'

export default function Background() {
  const {background, setBackground}= usePersistenceContext()

  const pickBackground = async () => {
    const backgrounds = [
      'Urchin',
      'Wanted',
      'Cult Initiate',
      'Thieve\'s Guild',
      'Banished',
      'Orphaned',
      'Wizard\'s Apprentice',
      'Jeweler',
      'Herbalist',
      'Barbarian',
      'Mercenary',
      'Sailor',
      'Acolyte',
      'Soldier',
      'Ranger',
      'Scout',
      'Minstrel',
      'Scholar',
      'Noble',
      'Chirurgeon',
    ]
    const choice = backgrounds[Math.floor(Math.random() * backgrounds.length)]
    setBackground(choice)
  }

  if (!background || background === '') {
    return (
      <Stack direction="row" spacing={0}>
        <TextField label="Background" disabled />
        <Button onClick={pickBackground}>Roll</Button>
      </Stack>
    )
  }

  return <TextField label="Background" value={background || ''} onChange={(ev) => setBackground(ev.target.value)} />
}
