import React from 'react'
import { Box, TextField, Typography } from '@mui/material'
import { usePersistenceContext } from './PersistenceContext'

export default function Armor() {
  const { ac, setAc } = usePersistenceContext()

  const handleAc = (ev: React.ChangeEvent<HTMLInputElement>) => {
    const newVal = ev.target.value
    if (newVal === '' || Number.isInteger(Number(newVal))) {
      setAc(ev.target.value)
    } else {
      console.log('Invalid value: ', newVal)
    }
  }
  
  return (
    <div style={{
      display: 'flex',
      border: '1px solid darkGray',
      padding: 10,
      alignItems: 'center',
      flexDirection: 'column',
      borderRadius: 4,
    }}>
      <Typography variant="h6">Armor Class</Typography>

      <Box width={60} sx={{ mt: 2, mb: 1 }}>
        <TextField value={ac || ''} size="small" onChange={handleAc}/>
      </Box>
    </div>
  )
}
