import React from 'react'
import { Button, Dialog, DialogContent, DialogTitle, Stack, Typography } from '@mui/material'
import { usePersistenceContext } from './PersistenceContext'
import { GRIMOIRE, Spell } from './Grimoire'

type SpellListProps = {
  selectedSpellNames: string[]
  handleClose: () => void
  onAddSpell: (spell: Spell) => void
  onDeleteSpell: (spellName: string) => void
}

export default function SpellList({ selectedSpellNames, handleClose, onAddSpell, onDeleteSpell }: SpellListProps) {
  const { heroPath } = usePersistenceContext()

  const useWizards = heroPath === 'Wizard'
  const usePriest = heroPath === 'Priest'
  const availableSpells = GRIMOIRE.filter(s => (s.wizard && useWizards) || (s.priest && usePriest)).filter(s => s.tier === 1)
  const spellComparator = (a: Spell, b: Spell) => {
    if (a.tier === b.tier) {
      return a.name.localeCompare(b.name)
    }
    return a.tier - b.tier
  }
  availableSpells.sort(spellComparator)

  const add = (spell: Spell) => onAddSpell(spell)

  return (
    <Dialog open onClose={handleClose}>
      <DialogTitle>Spell List</DialogTitle>
      <DialogContent>
        <Typography variant='body1' sx={{ mb: 2}}>
          Choose a spell…
        </Typography>

        {availableSpells.map(s => (
          <Stack key={s.name} direction='row' sx={{ alignItems: 'left', justifyContent: 'space-between', mb: 2 }}>
            <Stack key={s.name} direction='column' sx={{ alignItems: 'left', justifyContent: 'space-between', mb: 2 }}>
              <Stack key={s.name} sx={{ alignItems: 'center'}} direction='row'>
                <Typography variant='body1' sx={{ mr: 2 }}>{s.name}</Typography>
                <Typography variant='body2' sx={{ mr: 2 }}>Tier {s.tier}</Typography>
              </Stack>
              <Typography variant='caption'>Duration {s.duration}</Typography>
              <Typography variant='body2'>{s.description}</Typography>
            </Stack>
            {selectedSpellNames.includes(s.name) ? (
              <Button onClick={() => onDeleteSpell(s.name)}>Remove</Button>
            ) : (
              <Button onClick={() => add(s)}>Add</Button>
            )}
          </Stack>
        ))}
      </DialogContent>
    </Dialog>
  )
}
