import React, { useEffect, useState } from 'react'

import { Autocomplete, AutocompleteValue, Grid, IconButton, Stack, TextField } from '@mui/material'
import { Cancel } from '@mui/icons-material'
import { Item } from '../GameTypes'
import { ITEMS } from './GearList'
import Coinbag from './Coinbag'
import SubSlot from './SubSlot'
import { useInventoryContext } from '../InventoryContext'
import { usePersistenceContext } from '../PersistenceContext'

type ItemSlotProps = {
  idx: number,
}

export default function ItemSlot({ idx }: ItemSlotProps) {
  const [label, setLabel] = useState('')
  const [expendedCount, setExpendedCount] = useState(0)
  const [copper, setCopper] = useState(0)
  const [silver, setSilver] = useState(0)
  const [gold, setGold] = useState(0)
  const [note, setNote] = useState('')

  const { id: characterId } = usePersistenceContext()
  const { inventory, upsertItem, deleteItem, updateExpended, updateCoinbag, updateNote } = useInventoryContext()

  const thisSlot = inventory.find(i => i.index == `${idx}`)
  const thisDbId = thisSlot?.id

  const incrementExpended = () => {
    const newExpended = expendedCount + 1
    setExpendedCount(newExpended)
    if (thisDbId) {
      updateExpended(thisDbId, newExpended)
    }
  }

  const decrementExpended = () => {
    const newExpended = expendedCount - 1
    setExpendedCount(newExpended)
    if (thisDbId) {
      updateExpended(thisDbId, newExpended)
    }
  }

  useEffect(() => {
    if (thisSlot) {
      if (thisDbId == '19') {
        console.log('Got this slot: ', thisSlot)
      }
      setLabel(thisSlot.label || '')
      setExpendedCount(thisSlot.expended)
      setCopper(thisSlot.copper)
      setSilver(thisSlot.silver)
      setGold(thisSlot.gold)
      setNote(thisSlot.note || '')
    }
  }, [thisSlot])

  const chooseItem = async (itemName: string) => {
    setLabel('')

    if (!itemName && thisDbId) {
      deleteItem(thisDbId)
      return
    }

    const foundItem = ITEMS.find((i) => i.label !== '' && itemName.startsWith(i.label)) || null
    setLabel(foundItem?.label || '')
    upsertItem({
      id: thisDbId,
      characterId,
      index: `${idx}`,
      label: foundItem?.label || '',
      weight: foundItem?.weight || 1,
      note: '',
      quantity: foundItem?.qty || 1,
      expended: 0,
      copper: 0,
      silver: 0,
      gold: 0,
    })
  }

  const formatItem = (item: Item) => {
    if (!item) {
      return ''
    }
    return item.label
  }

  const filteredItems = ITEMS.map(formatItem)

  const updateCopper = (newCopper: number) => {
    setCopper(newCopper)
    if (thisDbId) {
      updateCoinbag(thisDbId, newCopper, silver, gold)
    }
  }

  const updateSilver = (newSilver: number) => {
    setSilver(newSilver)
    if (thisDbId) {
      updateCoinbag(thisDbId, copper, newSilver, gold)
    }
  }

  const updateGold = (newGold: number) => {
    setGold(newGold)
    if (thisDbId) {
      updateCoinbag(thisDbId, copper, silver, newGold)
    }
  }

  const handleNote = (newNote: string) => {
    setNote(newNote)
    if (thisDbId) {
      updateNote(thisDbId, newNote)
    }
  }

  if (label === 'Coinbag') {
    return (
      <Coinbag onClear={() => chooseItem('')}
               copper={copper}
               setCopper={updateCopper}
               silver={silver}
               setSilver={updateSilver}
               gold={gold}
               setGold={updateGold}
      />
    )
  }

  if (label === 'Other Item' || label === 'Other Item (weightless)') {
    console.log('Got this item: ', thisSlot)
    console.log("Got an other item withg note ", note)
    return (
      <Grid item xs={6}>
        <Stack direction="row" sx={{ mt: 2, ml: 1, mr: 1, alignItems: 'center' }}>
          <TextField label="Other Item"
                     variant="outlined"
                     fullWidth
                     value={note}
                     onChange={(ev: React.ChangeEvent<HTMLInputElement>) => handleNote(ev.target.value)}
          />
          <IconButton onClick={() => chooseItem('')}><Cancel /></IconButton>
        </Stack>
      </Grid>
    )
  }

  return (
    <Grid item xs={6}>
      <Stack direction="row" sx={{ mt: 2, ml: 1, mr: 1, alignItems: 'center' }}>
        <Autocomplete id={`${idx}`}
                      options={filteredItems}
                      style={{ width: '100%' }}
                      renderInput={(params) => <TextField {...params} />}
                      value={label || ''}
                      onChange={(v, vv: AutocompleteValue<string, undefined, undefined, undefined>) => chooseItem(vv || '')}

        />
        {(thisSlot || {}).quantity == 2 && (
          <div style={{ marginLeft: 10 }}>
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount === 2} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount === 1} />
          </div>
        )}
        {(thisSlot || {}).quantity == 3 && (
          <div style={{ display: 'flex', marginLeft: 10 }}>
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 2} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 1} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 0} />
          </div>
        )}
        {(thisSlot || {}).quantity == 4 && (
          <div style={{ marginLeft: 10 }}>
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 3} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 2} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 1} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 0} />
          </div>
        )}
        {(thisSlot || {}).quantity == 6 && (
          <div style={{ marginLeft: 10 }}>
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 5} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 4} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 3} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 2} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 1} />
            <SubSlot onIncrement={incrementExpended} onDecrement={decrementExpended} expended={expendedCount > 0} />
          </div>
        )}
      </Stack>
      {/* {(thisSlot && thisSlot.note) && (
        <TextField sx={{ m: 2 }} label="Note" variant="standard" fullWidth />
      )} */}
    </Grid>
  )
}
