import React, { useEffect, useState } from 'react'
import { FormControl, Grid, InputLabel, MenuItem, Select, SelectChangeEvent, Stack } from '@mui/material'
import { usePersistenceContext } from './PersistenceContext'

export const HUMAN = 'Human'
export const ELF = 'Elf'
export const DWARF = 'Dwarf'
export const HALFLING = 'Halfling'
export const HALFORC = 'Half-Orc'
export const GOBLIN = 'Goblin'
export const KOBOLD = 'Kobold'

const ancestries = [
  { type: HUMAN, note: 'Bold, adaptable, and diverse people who learn quickly and accomplish mighty deeds. You gain one additional talent roll at 1st level.' },
  { type: ELF, note: 'Ethereal, graceful people who revere knowledge and beauty. Elves see far and live long. You get a +1 bonus to attack rolls with ranged weapons or a +1 bonus to spellcasting checks.' },
  { type: DWARF, note: 'Brave, stalwart folk as sturdy as the stone kingdoms they carve inside mountains. Start with +2 HP. Roll your hit point gains with advantage.' },
  { type: HALFLING, note: 'Small, cheerful country folk with mischievous streaks. They enjoy life’s simple pleasures. Once per day, you can become invisible for 3 rounds.' },
  { type: GOBLIN, note: 'Green, clever beings who thrive in dark, cramped places. As fierce as they are tiny. You cannot be surprised.' },
  { type: KOBOLD, note: 'Tiny, humanoid dog-lizards with a penchant for tinkering, trap building, and a hint of magic. You get +1 to spellcasting checks or may begin each session with a luck token.' },
  { type: HALFORC, note: 'Towering, tusked warriors who are as daring as humans and as relentless as orcs. You have a +1 bonus to attack and damage rolls with melee weapons.' },
]

export default function Ancestry() {
  const {ancestry, setAncestry} = usePersistenceContext()

  const handleAncestry = (ev: SelectChangeEvent<string>) => {
    const _a = ev.target.value
    setAncestry(_a)
  }

  return (
    <Stack style={{ width: '100%' }}>
      <FormControl fullWidth style={{ flex: 1, justifyContent: 'center' }}>
        <InputLabel id="ancestry">Ancestry</InputLabel>
        <Select value={ancestry || ''}
                onChange={handleAncestry}
                variant="outlined"
                label="Ancestry"
                renderValue={(selected) => selected}
        >
          <MenuItem value=''></MenuItem>
          {
            ancestries.map(a => (
              <MenuItem key={a.type} value={a.type} style={{ width: 600 }}>
                <Grid container style={{ alignItems: 'center' }}>
                  <Grid item xs={12}>
                    <b>{a.type}</b>
                  </Grid>

                  <Grid item xs={1}></Grid>
                  <Grid item xs={11} sx={{ textWrap: 'wrap' }}>
                    {a.note}
                  </Grid>
                </Grid>
              </MenuItem>
            ))
          }
        </Select>
      </FormControl>

      <div style={{ flex: 1, marginTop: 10, marginBottom: 20 }}>
        { (ancestries.find(a => a.type === ancestry) || {}).note }
      </div>
    </Stack>
  )
}
