import React, { useContext, useEffect, createContext, useState, useRef } from 'react'
// @ts-ignore
import DiceBox from "@3d-dice/dice-box"
import { Button, Stack, Typography } from '@mui/material'
import { useMessagesCtx } from './MessagesContext'
import { usePersistenceContext } from './PersistenceContext'

var _diceBox: any = null

const noOp = (spec: string, callback: (result: number) => void) => {
  console.log('Context not initialized')
}
const clearNoOp = () => {
  console.log('Context not initialized')
}

const RollerCtx = createContext<{ 
  rollDice: (spec: string, callback: (result: number) => void) => void,
  clearDice: () => void,
}>({ rollDice: noOp, clearDice: clearNoOp })

export const useRollerContext = () => useContext(RollerCtx)

type RollResultDisplay = {
  spec: string,
  total: number
}

export default function DiceContext({ children }: { children: React.ReactNode }) {
  const [showingRoll, setShowingRoll] = useState(false)
  const [log, setLog] = useState<RollResultDisplay[]>([])
  const {name} = useMessagesCtx()
  const { supabase } = usePersistenceContext()
  const logRef = useRef(log)

  useEffect(() => {
    if (!_diceBox) {
      _diceBox = new DiceBox({
        container: "#dice-box",
        assetPath: "/assets/dice-box/",
        // theme: "blueGreenMetal",
        // theme: "diceOfRolling",
        // theme: "blueGreenMetal",
        // themeColor: "#f11111",
        // themeColor: "#00ff00",
        offscreen: true,
        gravity: 10,
        scale: 8,
        settleTimeout: 4000,
        throwForce: 10,
      })

      _diceBox.init().then(() => {
        console.log('Dice box ready')
      });
    }
  }, [])

  const reportResult = (spec: string, result: any[]): number => {
    let total = 0
    for (let i = 0; i < result.length; i++) {
      total += result[i].value
    }
    const newResult = { spec, total }
    setLog((prevLog) => [newResult, ...prevLog])

    const myChannel = supabase.channel('player-updates')
    const payload = { name, message: `${name} rolled ${spec} = ${total}`}
    myChannel.send({
      type: 'broadcast',
      event: 'die roll',
      payload
    })
    console.log('Just sent this payload: ', payload)

    return total
  }

  const doRollDice = (spec: string, callback: (result: number) => void) => {
    _diceBox.add(spec).then((r: any[]) => callback(reportResult(spec, r)))
    setShowingRoll(true)
  }

  const doClearDice = () => {
    _diceBox.clear()
    setShowingRoll(false)
    setLog([])
  }

  return (
    <RollerCtx.Provider value={{ rollDice: doRollDice, clearDice: doClearDice }}>
      <div id="dice-box"
           style={{ 
            position: "fixed", 
            width: 680, 
            top: 0, 
            left: 0, 
            zIndex: 1000, 
            height: 500, 
            pointerEvents: "none",
            display: "flex",
            flexDirection: "row",
          }} 
      />
      {
          showingRoll && (
            <div style={{ 
              position: "fixed", 
              top: 10, 
              left: 710,
              display: "flex",
              flexDirection: "column",
            }} 
            >
              <Stack direction="column" spacing={1}>
                <Button style={{ display: "flex", flex: 1, zIndex: 2000, pointerEvents: "auto", backgroundColor: "white" }} 
                        variant="outlined"
                        onClick={() => doClearDice()}
                >
                  Clear Dice
                </Button>
                <Stack direction="column" spacing={1}>
                  { log.map((result, idx) => (
                    <Typography key={idx}>
                      {result.spec} = {result.total}
                    </Typography>
                  ))}
                </Stack>
              </Stack>
              {/* <Typography>
                { rollLine }
              </Typography> */}
            </div>
          )
        }

      {children}
    </RollerCtx.Provider>
  )
}
