import React, { useState, useContext, useEffect, createContext } from 'react'
import { usePersistenceContext } from './PersistenceContext'
import { ITEMS } from './Inventory/GearList'

const noOp = () => {}

export type DbInventoryItem = {
  id?: string,
  characterId: string,
  index: string,
  label?: string,
  note?: string,
  weight: number,
  quantity: number,
  expended: number,
  copper: number,
  silver: number,
  gold: number,
}

const InventoryCtx = createContext<{
  inventory: DbInventoryItem[],
  upsertItem: (item: DbInventoryItem) => void,
  deleteItem: (dbId: string) => void,
  updateExpended: (dbId: string, expended: number) => void,
  updateCoinbag: (dbId: string, copper: number, silver: number, gold: number) => void,
  updateNote: (dbId: string, note: string) => void,
}>({
  inventory: [],
  upsertItem: noOp,
  deleteItem: noOp,
  updateExpended: noOp,
  updateCoinbag: noOp,
  updateNote: noOp,
})

export const useInventoryContext = () => useContext(InventoryCtx)

let timers: { [dbId: string]: any } = {}

export default function InventoryContext({ children }: { children: React.ReactNode }) {
  const [inventory, setInventory] = useState<DbInventoryItem[]>([])
  const { supabase, id: characterId } = usePersistenceContext()

  const loadInventory = async () => {
    if (!characterId) {
      console.log('No character ID, not loading inventory')
      return
    }

    const { data, error } = await supabase.from('InventorySlot').select().eq('characterId', characterId)
    if (data) {
      setInventory(data.map((i: any) => ({ 
        id: i.id, 
        characterId: i.characterId, 
        index: i.index, 
        label: i.label, 
        note: i.note,
        weight: i.weight, 
        quantity: i.quantity, 
        expended: i.expended, 
        copper: i.copper, 
        silver: i.silver, 
        gold: i.gold, 
      })))
    }
    if (error) {
      console.error('Error loading inventory', error)
    }
  }

  useEffect(() => {
    setInventory([])
    loadInventory()
  }, [characterId, supabase])

  const debouncedUpdate = (item: DbInventoryItem) => {
    if (!item.id) {
      console.error("Trying to update item without ID")
      return
    }
    if (timers[item.id]) {
      clearTimeout(timers[item.id])
    }
    timers[item.id] = setTimeout(async () => {
      await supabase.from('InventorySlot').upsert(item)
      loadInventory()
    }, 500)
  }

  const upsertItem = async (item: DbInventoryItem) => {
    if (!item.id) {
      await supabase.from('InventorySlot').upsert(item)
      loadInventory()
    } else {
      debouncedUpdate(item)
    }
  }

  const updateExpended = async (dbId: string, expended: number) => {
    await supabase.from('InventorySlot').update({ expended }).eq('id', dbId)
  }

  const updateCoinbag = async (dbId: string, copper: number, silver: number, gold: number) => {
    await supabase.from('InventorySlot').update({ copper, silver, gold }).eq('id', dbId)
  }

  const updateNote = async (dbId: string, note: string) => {
    await supabase.from('InventorySlot').update({ note }).eq('id', dbId)
  }

  const deleteItem = async (dbId: string) => {
    const result = await supabase.from('InventorySlot').delete().eq('id', dbId)
    setInventory(inventory.filter(i => i.id !== dbId))
  }

  return (
    <InventoryCtx.Provider value={{ inventory, upsertItem, deleteItem, updateExpended, updateCoinbag, updateNote }}>
      {children}
    </InventoryCtx.Provider>
  )
}
