import React, { useState, useContext, useEffect, createContext } from 'react'
import { usePersistenceContext } from './PersistenceContext'
import { ClassFeatType } from './Feats'

const noOp = () => {}

export type DbClassFeat = { 
  dbId?: string, 
  characterId: string, 
  description: string
  hasNote: boolean
  noteLabel?: string
  note?: string,
}

const FeatsCtx = createContext<{
  feats: DbClassFeat[],
  addFeat: (feat: ClassFeatType) => void,
  updateFeat: (dbId: string, note: string) => void,
  deleteFeat: (dbId: string) => void,
}>({
  feats: [],
  addFeat: noOp,
  updateFeat: noOp,
  deleteFeat: noOp,
})

export const useFeatsContext = () => useContext(FeatsCtx)

let timers: { [key: string]: any } = {}

export default function FeatsContext({ children }: { children: React.ReactNode }) {
  const [feats, setFeats] = useState<DbClassFeat[]>([])
  const { supabase, id: characterId } = usePersistenceContext()

  useEffect(() => {
    setFeats([])
    loadFeats()
  }, [characterId, supabase])

  const loadFeats = async () => {
    if (!characterId) {
      return
    }

    const { data, error } = await supabase.from('ClassFeat').select().eq('characterId', characterId)
    if (data) {
      setFeats(data.map((f: any) => ({ dbId: f.id, characterId: f.characterId, description: f.description, hasNote: f.hasNote, noteLabel: f.noteLabel, note: f.note })))
    }
    if (error) {
      console.error('Error loading class feats', error)
    }
  }

  const addFeat = async (feat: ClassFeatType) => { 
    const payload: DbClassFeat = { characterId, description: feat.description, hasNote: feat.hasNote, note: '', noteLabel: feat.noteLabel }
    const result = await supabase.from('ClassFeat').upsert(payload).select()
    console.log('Inserted new feat: ', result)
    const newRow = result.data[0]
    newRow.dbId = newRow.id
    setFeats([...feats, newRow])
  }

  const debouncedFeatNoteUpdate = (featId: string, featNote: string) => {
    if (timers[featId]) {
      clearTimeout(timers[featId])
    }
    timers[featId] = setTimeout(async () => {
      console.log('Updating feat note', featId, featNote)
      const r = await supabase.from('ClassFeat').update({ note: featNote }).eq('id', featId)
      console.log(r)
    }, 500)
  }

  const updateFeat = (featId: string, featNote: string) => {
    const newFeats: DbClassFeat[] = []
    for (const feat of feats) {
      if (feat.dbId === featId) {
        const ff = {...feat}
        ff.note = featNote
        newFeats.push(ff)
        debouncedFeatNoteUpdate(featId, featNote)
      } else {
        newFeats.push(feat)
      }
    }
    setFeats(newFeats)
  }

  const deleteFeat = async (featId: string) => {
    await supabase.from("ClassFeat").delete().eq("id", featId)
    const newFeats = feats.filter(f => f.dbId !== featId)
    setFeats(newFeats)
  }

  return (
    <FeatsCtx.Provider value={{
      feats,
      addFeat,
      updateFeat,
      deleteFeat,
    }}>
      {children}
    </FeatsCtx.Provider>
  )
}
