import React, { useState } from 'react'
import { Button, Stack, TextField, Typography } from '@mui/material'
import { useRollerContext } from './DiceContext'
import { DESERT_RIDER, FIGHTER, PIT_FIGHTER, RANGER, SEA_WOLF, THIEF, WITCH, WIZARD } from './HeroPath'
import { usePersistenceContext } from './PersistenceContext'

export default function HitPoints() {
  const { heroPath, curHp, setCurHp, maxHp, setMaxHp } = usePersistenceContext()

  const rollerCtx = useRollerContext()
  const rollHitPoints = () => {
    let spec = "1d6"
    if (heroPath === FIGHTER) {
      spec = "1d8"
    } else if (heroPath === THIEF) {
      spec = "1d4"
    } else if (heroPath === WIZARD) {
      spec = "1d4"
    } else if (heroPath === RANGER) {
      spec = "1d8"
    } else if (heroPath === WITCH) {
      spec = "1d4"
    } else if (heroPath === DESERT_RIDER) {
      spec = "1d8"
    } else if (heroPath === PIT_FIGHTER) {
      spec = "1d8"
    } else if (heroPath === SEA_WOLF) {
      spec = "1d8"
    } else {
      spec = "1d6"
    }
    rollerCtx.rollDice(spec, (result: number) => {})
  }

  const handleCur = (ev: React.ChangeEvent<HTMLInputElement>) => {
    const newVal = ev.target.value
    if (newVal === '' || Number.isInteger(Number(newVal))) {
      setCurHp(ev.target.value)
    } else {
      console.log('Invalid value: ', newVal)
    }
  }

  const handleMax = (ev: React.ChangeEvent<HTMLInputElement>) => {
    const newVal = ev.target.value
    if (newVal === '' || Number.isInteger(Number(newVal))) {
      setMaxHp(ev.target.value)
    } else {
      console.log('Invalid value: ', newVal)
    }
  }

  return (
    <div style={{
      display: 'flex',
      border: '1px solid darkGray',
      padding: 10,
      alignItems: 'center',
      flexDirection: 'column',
      borderRadius: 4,
    }}>
      <Typography variant="h6">Hit Points</Typography>
      <Stack direction="row" width={120} sx={{ mt: 2, mb: 1 }}>
        <TextField value={curHp || ''} type="num" onChange={handleCur} size="small" label="Cur"/>
        &nbsp;
        <TextField value={maxHp || ''} type="num" onChange={handleMax} size="small" label="Max"/>
      </Stack>
      <Button variant="outlined" color="primary" size="small" onClick={rollHitPoints}>Roll</Button>
    </div>
  )
}
