import React from 'react'

import { Dialog, DialogContent, FormControl, InputLabel, MenuItem, Select, Stack, TextField } from '@mui/material'

import Ancestry from './Ancestry'
import HeroPath from './HeroPath'
import Spacer from './Spacer'
import CharacterFeatures from './CharacterFeatures'
import Armor from './Armor'
import Inventory from './Inventory/Inventory'
import Attributes from './Attributes'
import HitPoints from './HitPoints'
import { useRollerContext } from './DiceContext'
import { usePersistenceContext } from './PersistenceContext'
import Experience from './Experience'
import InventoryContext from './InventoryContext'
import Spells from './Spells'
import Background from './Background'


export default function Character() {
  const { name, setName, alignment, setAlignment, heroPath } = usePersistenceContext()

  const handleName = (ev: React.ChangeEvent<HTMLInputElement>) => {
    const newName = ev.target.value
    setName(newName)
  }

  const rollerCtx = useRollerContext()
  const roll = (spec: string) => {
    rollerCtx.rollDice(spec, (result: number) => {
      console.log("Result: ", result)
    })
  }

  return (
    <div style={{display: 'flex', marginTop: 10, flexDirection: 'column', width: 680 }}>
      <Stack direction="row" style={{ display: 'flex', justifyContent: 'space-between' }}>
        <TextField sx={{ flex: 5 }} label="Name" value={name || ''} onChange={handleName} />
        <Spacer />
        <img src="/icons/dice-d4-outline.svg" width="40" onClick={() => roll("1d4")}/>
        <img src="/icons/dice-d6-outline.svg" width="40" onClick={() => roll("1d6")}/>
        <img src="/icons/dice-d8-outline.svg" width="40" onClick={() => roll("1d8")}/>
        <img src="/icons/dice-d10-outline.svg" width="40" onClick={() => roll("1d10")}/>
        <img src="/icons/dice-d12-outline.svg" width="40" onClick={() => roll("1d12")}/>
        <img src="/icons/dice-d20-outline.svg" width="40" onClick={() => roll("1d20")}/>
      </Stack>
      <Spacer/>

      <Attributes />

      <Stack direction="row"
            justifyContent="space-between"
            style={{ marginTop: 20 }}
      >
        <Ancestry />
        <Spacer />
        <HeroPath />
      </Stack>

      <Stack direction="row" style={{ display: 'flex', justifyContent: 'space-around' }}>
        <Stack direction="row">
          <HitPoints />
          <Spacer />
          <Armor />
          <Spacer />
          <Experience />
        </Stack>
        <Spacer />
        <Stack direction="column" sx={{ flex: 1, justifyItems: 'start' }}>
          <Background />
          <Spacer/>
          <FormControl fullWidth style={{ justifyContent: 'center' }}>
            <InputLabel id="ancestry">Alignment</InputLabel>
            <Select value={alignment || ''}
                    onChange={(ev) => setAlignment(ev.target.value)}
                    variant="outlined"
                    label="Alignment"
                    renderValue={(selected) => selected}
            >
              <MenuItem value="Lawful">Lawful</MenuItem>
              <MenuItem value="Neutral">Neutral</MenuItem>
              <MenuItem value="Chaotic">Chaotic</MenuItem>
            </Select>
          </FormControl>
        </Stack>
      </Stack>

      <CharacterFeatures />

      {
        (heroPath === 'Wizard' || heroPath === 'Priest') && <Spells />
      }

      <InventoryContext>
        <Inventory/>
      </InventoryContext>
    </div>
  )
}
