import React, { useState, useContext, useEffect, createContext } from 'react'
import { createClient } from '@supabase/supabase-js'

const noOp = () => {}

const PersistenceCtx = createContext<{
  supabase: any, setSupabase: (supabase: any) => void,
  id: string, setId: (id: string) => void,
  name: string, setName: (name: string) => void,
  ancestry: string, setAncestry: (ancestry: string) => void,
  heroPath: string, setHeroPath: (charClass: string) => void,
  background: string, setBackground: (background: string) => void,
  alignment: string, setAlignment: (alignment: string) => void,
  xp: string, setXp: (xp: string) => void,
  ac: string | null, setAc: (ac: string) => void,
  str: string | null, setStr: (str: string) => void,
  dex: string | null, setDex: (dex: string) => void,
  con: string | null, setCon: (con: string) => void,
  int: string | null, setInt: (int: string) => void,
  wis: string | null, setWis: (wis: string) => void,
  cha: string | null, setCha: (cha: string) => void,
  curHp: string | null, setCurHp: (curHp: string) => void,
  maxHp: string | null, setMaxHp: (maxHp: string) => void,
  features: FeatureDefinition[], updateFeature: (featureName: string, featureValue: string) => void,
}>({
  supabase: null, setSupabase: noOp,
  id: '', setId: noOp,
  name: '', setName: noOp, 
  ancestry: '', setAncestry: noOp, 
  heroPath: '', setHeroPath: noOp,
  background: '', setBackground: noOp,
  alignment: '', setAlignment: noOp,
  xp: '0', setXp: noOp,
  ac: null, setAc: noOp,
  str: null, setStr: noOp,
  dex: null, setDex: noOp,
  con: null, setCon: noOp,
  int: null, setInt: noOp,
  wis: null, setWis: noOp,
  cha: null, setCha: noOp,
  curHp: null, setCurHp: noOp,
  maxHp: null, setMaxHp: noOp,
  features: [], updateFeature: noOp,
})

export const usePersistenceContext = () => useContext(PersistenceCtx)

let timers: { [key: string]: any } = {}

type FeatureDefinition = {
  id: string,
  name: string,
  note: string,
}

export default function PersistenceContext({ children }: { children: React.ReactNode }) {
  const [supabase, setSupabase] = useState<any>(null)
  
  const [id, setId] = useState<string>('')
  const [name, setName] = useState<string>('')
  const [ancestry, setAncestry] = useState<string>('')
  const [heroPath, setHeroPath] = useState<string>('')
  const [background, setBackground] = useState<string>('')
  const [alignment, setAlignment] = useState<string>('')
  const [str, setStr] = useState<string>('')
  const [dex, setDex] = useState<string>('')
  const [con, setCon] = useState<string>('')
  const [int, setInt] = useState<string>('')
  const [wis, setWis] = useState<string>('')
  const [cha, setCha] = useState<string>('')
  const [curHp, setCurHp] = useState<string>('')
  const [maxHp, setMaxHp] = useState<string>('')
  const [level, setLevel] = useState<string>('1')
  const [xp, setXp] = useState<string>('0')
  const [ac, setAc] = useState<string>('10')

  const [features, setFeatures] = useState<FeatureDefinition[]>([])

  const sendUpdate = async (payload: any) => {
    if (id) {
      await supabase.from("Character").update(payload).eq("id", id)
    }
  }

  const sendUpdateDebounced = (payload: { [key: string]: string }) => {
    for (const key in payload) {
      if (timers[key]) {
        clearTimeout(timers[key])
      }
      timers[key] = setTimeout(() => {
        sendUpdate(payload)
      }, 2500)
    }
  }
  useEffect(() => sendUpdateDebounced({ name }), [name])
  useEffect(() => sendUpdateDebounced({ alignment }), [alignment])
  useEffect(() => sendUpdateDebounced({ background }), [background])
  useEffect(() => sendUpdateDebounced({ str }), [str])
  useEffect(() => sendUpdateDebounced({ dex }), [dex])
  useEffect(() => sendUpdateDebounced({ con }), [con])
  useEffect(() => sendUpdateDebounced({ int }), [int])
  useEffect(() => sendUpdateDebounced({ wis }), [wis])
  useEffect(() => sendUpdateDebounced({ cha }), [cha])
  useEffect(() => sendUpdateDebounced({ ancestry }), [ancestry])
  useEffect(() => sendUpdateDebounced({ heroPath }), [heroPath])
  useEffect(() => sendUpdateDebounced({ xp }), [xp])
  useEffect(() => sendUpdateDebounced({ ac }), [ac])
  useEffect(() => sendUpdateDebounced({ curHp }), [curHp])
  useEffect(() => sendUpdateDebounced({ maxHp }), [maxHp])

  const sendFeatureUpdateDebounced = (featureId: any, featureName: string, note: string) => {
    const key = "feature-" + featureName
    if (timers[key]) {
      clearTimeout(timers[key])
    }
    timers[key] = setTimeout(async () => {
      console.log("Sending this update to supabase: ", { featureName, note })
      const r = await supabase.from("FeatureNote").update({ featureName, note }).eq("id", featureId)
      console.log(r)
    }, 2500)
  }

  const updateFeature = async (featureName: string, note: string) => {
    console.log('In updateFeature with ', featureName, note)
    const newFeatures = []
    let found = false
    for (const f of features) {
      if (f.name === featureName) {
        console.log('Found the feature, updating id ', f.id)
        newFeatures.push({ id: f.id, name: f.name, note })
        sendFeatureUpdateDebounced(f.id, featureName, note)
        found = true
      } else {
        newFeatures.push(f)
      }
    }

    if (!found) {
      // We can't debounce the insert because we want to be sure to get an ID synchronously
      const result = await supabase.from("FeatureNote").upsert({ characterId: id, featureName, note }).select()
      const newRow = result.data[0]
      newFeatures.push({ id: newRow.id, name: featureName, note })
    }
    setFeatures(newFeatures)
  }

  useEffect(() => {
    // Reset all state when the ID changes
    setName('')
    setAncestry('')
    setHeroPath('')
    setBackground('')
    setAlignment('')
    setXp('0')
    setAc('10')
    setStr('')
    setDex('')
    setCon('')
    setInt('')
    setWis('')
    setCha('')
    setCurHp('')
    setMaxHp('')
    setFeatures([])

    if (id) {
      console.log('Loading character data for', id)
      const loadCharacter = async () => {
        const { data, error } = await supabase
          .from("Character")
          .select()
          .eq("id", id)

        if (data) {
          const c = data[0]
          setName(c.name)
          setAncestry(c.ancestry)
          setHeroPath(c.heroPath)
          setBackground(c.background)
          setAlignment(c.alignment)
          setXp(c.xp)
          setAc(c.ac)
          setStr(c.str || '')
          setDex(c.dex)
          setCon(c.con)
          setInt(c.int)
          setWis(c.wis)
          setCha(c.cha)
          setCurHp(c.curHp)
          setMaxHp(c.maxHp)
        }
        if (error) {
          console.error('Error loading character', error)
        }
      }
      loadCharacter()

      const loadFeatures = async () => {
        const { data, error } = await supabase
          .from("FeatureNote")
          .select()
          .eq("characterId", id)

        if (data) {
          setFeatures(data.map((f: any) => ({ id: f.id, name: f.featureName, note: f.note })))
        }
        if (error) {
          console.error('Error loading features', error)
        }
      }
      loadFeatures()
    }
  }, [id])

  const doLogin = async () => {
    const supabase = createClient('https://qzchdwazbhrriqetpvhy.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InF6Y2hkd2F6YmhycmlxZXRwdmh5Iiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzY1NjQ2MzEsImV4cCI6MjA1MjE0MDYzMX0.l3LuaSanNPBGDTWaz48EXpcSEdWqleo7Zz2PychCa1c')
    console.log('Freshly created supabase is now', supabase)
    await supabase.auth.signInAnonymously()
    console.log('Signed in!!')
    setSupabase(supabase)
  }

  useEffect(() => {
    console.log('Logging in')
    doLogin()
  }, [])

  return (
    <PersistenceCtx.Provider value={{
      supabase, setSupabase,
      id, setId,
      name, setName,
      ancestry, setAncestry,
      heroPath, setHeroPath,
      background, setBackground,
      alignment, setAlignment,
      xp, setXp,
      ac, setAc,
      str, setStr,
      dex, setDex,
      con, setCon,
      int, setInt,
      wis, setWis,
      cha, setCha,
      curHp, setCurHp,
      maxHp, setMaxHp,
      features, updateFeature,
    }}>
      {children}
    </PersistenceCtx.Provider>
  )
}
