function CharacterFeatureWidget({ feature: f }: { feature: Feature }) {
  const { features, updateFeature } = usePersistenceContext()

  return (
    <Stack sx={{ alignItems: 'center' }} direction="row">
      <Stack sx={{ flex: 3 }} direction="column">
        <p style={{ padding: 0, marginTop: 10, marginBottom: 5 }}>
          <strong>{f.featureName}</strong>
          <small> ({f.filter})</small>
        </p>
        {f.feature && (
          <p style={{ flex: 3, padding: 0, marginTop: 10, marginLeft: 15, marginBottom: 5 }}>
            {f.feature}
          </p>
        )}
      </Stack>
      {f.hasOption && (
        <Stack style={{ flex: 2, marginTop: 20, marginLeft: 10 }}>
          <TextField label={f.featureName} 
                    value={features.find(ff => ff.name === f.featureName)?.note || ''}
                    onChange={(ev) => updateFeature(f.featureName, ev.target.value)}
          />
        </Stack>
      )}
      {f.options && (
        <Select value={features.find(ff => ff.name === f.featureName)?.note || ''}
                onChange={(ev) => updateFeature(f.featureName, ev.target.value)}
                displayEmpty
                sx={{ width: 200, ml: 1 }}
        >
          <MenuItem value="">Pick One…</MenuItem>
          {f.options.map(o => (
            <MenuItem key={o} value={o}>{o}</MenuItem>
          ))}
        </Select>
      )}
    </Stack>
  )
}

function CharacterFeatWidget({ feat }: { feat: DbClassFeat }) {
  const { updateFeat, deleteFeat } = useFeatsContext()

  const doDelete = () => {
    if (feat.dbId) {
      deleteFeat(feat.dbId)
    }
  }

  return (
    <Stack sx={{ alignItems: 'center' }} direction="row">
      <Stack sx={{ flex: 3 }} direction="column">
        <p style={{ padding: 0, marginTop: 10, marginBottom: 5 }}>
          <strong>Feat</strong>
        </p>
        <p style={{ flex: 3, padding: 0, marginTop: 10, marginLeft: 15, marginBottom: 5 }}>
          {feat.description}
        </p>
      </Stack>
      <IconButton sx={{ color: "#aaa", mt: 2 }} onClick={doDelete}>
        <Delete />
      </IconButton>
      <Stack style={{ flex: 2, marginTop: 20, marginLeft: 10 }}>
        {feat.hasNote && (
          <TextField label={feat.noteLabel || ''}
                    value={feat.note || ''}
                    onChange={(ev) => updateFeat(feat.dbId || '', ev.target.value)}
          />
        )}
      </Stack>
    </Stack>
  )
}

export default function CharacterFeatures() {
  const { ancestry, heroPath } = usePersistenceContext()
  const { feats } = useFeatsContext()

  return (
    <Stack style={{ flex: 1, border: '1px solid darkGray', padding: 10, marginTop: 20 }}>
      <Typography variant="h6" component='span'>
        Character Features
      </Typography>

      { ANCESTRY_FEATURES.filter(a => a.filter === ancestry).map(f => (
        <CharacterFeatureWidget key={f.featureName} feature={f} />
      ))}

      { PATH_FEATURES.filter(f => f.filter === heroPath).map(f => (
        <CharacterFeatureWidget key={f.featureName} feature={f} />
      ))}

      { ANCESTRY_LANGUAGES.filter(a => a.filter === ancestry).map(f => (
        <CharacterFeatureWidget key={f.featureName} feature={f} />
      ))}

      { PATH_LANGUAGES.filter(a => a.filter === heroPath).map(f => (
        <CharacterFeatureWidget key={f.featureName} feature={f} />
      ))}

      { feats.map(f => (
        <CharacterFeatWidget key={f.dbId} feat={f} />
      ))}

      <Feats />
    </Stack>
  )
}
