import React, { useState, useContext, useEffect, createContext } from 'react'
import { usePersistenceContext } from './PersistenceContext'

const MessagesCtx = createContext<{
  name: string,
  setName: (name: string) => void,
  messages: string[],
}>({
  name: 'Player',
  setName: () => {},
  messages: [],
})

export const useMessagesCtx = () => useContext(MessagesCtx)

export default function MessagesContext({ children }: { children: React.ReactNode }) {
  const [messages, setMessages] = useState<string[]>([])
  const [name, setName] = useState<string>('Player')
  const { supabase } = usePersistenceContext()


  const messageReceived = (broadcast: any) => {
    console.log('Recieved this message: ', broadcast)
    if (broadcast.payload.name === name) {
      return
    }
    const newMessages = [...messages]
    while (newMessages.length > 3) {
      console.log('Popping message')
      newMessages.pop()
    }
    newMessages.unshift(broadcast.payload.message)
    setMessages(newMessages)
  }

  const setup = () => {
    if (!supabase) {
      return
    }
    const myChannel = supabase.channel('player-updates')

    // Subscribe to the Channel
    myChannel
      .on(
        'broadcast',
        { event: '*' },
        messageReceived
      )
      .subscribe()
  }

  useEffect(setup, [supabase, messages])

  const handleName = (name: string) => {
    setName(name)
    localStorage.setItem('name', name)
  }

  return (
    <MessagesCtx.Provider value={{ name, setName: handleName, messages }}>
      {children}
    </MessagesCtx.Provider>
  )
}
