// Generate a random name with a vaguely fantasy feel.
export function RandomName() {
  const prefixes = [
    'Ae', 'Al', 'Ar', 'Ba', 'Be', 'Bo', 'Ce', 'Co', 'Da', 'De', 'Do', 'Fa', 'Fe', 'Fo', 'Ga', 'Ge', 'Go', 'Ha', 'He', 'Ho', 'Ja', 'Je', 'Jo', 'Ka', 'Ke', 'Ko', 'La', 'Le', 'Lo', 'Ma', 'Me', 'Mo', 'Na', 'Ne', 'No', 'Pa', 'Pe', 'Po', 'Qu', 'Ra', 'Re', 'Ro', 'Sa', 'Se', 'So', 'Ta', 'Te', 'Th', 'To', 'Va', 'Ve', 'Vo', 'Wa', 'We', 'Wo', 'Xa', 'Xe', 'Xi', 'Xo', 'Ya', 'Ye', 'Yo', 'Za', 'Ze', 'Zo'
  ]
  const suffixes = [
    'bar', 'bri', 'bur', 'dar', 'din', 'dor', 'fan', 'fir', 'fur', 'kas', 'kur', 'lor', 'mir', 'morn', 'nan', 'nar', 'nis', 'nor', 'pur', 'rad', 'ran', 'ras', 'rin', 'ris', 'ros', 'run', 'rus', 'sal', 'san', 'sin', 'sol', 'sul', 'tan', 'tas', 'tin', 'tor', 'tur', 'val', 'van', 'vor', 'wen', 'win', 'wor', 'yas', 'yur', 'zor'
  ]
  let name = prefixes[Math.floor(Math.random() * prefixes.length)]
  name += suffixes[Math.floor(Math.random() * suffixes.length)]

  if (Math.random() > 0.75) {
    name += suffixes[Math.floor(Math.random() * suffixes.length)]
  }

  return name
}
