import { Stack, Tab, Tabs, ThemeProvider, Typography, createTheme } from '@mui/material'
import Character from './Character'
import { useEffect, useState } from 'react'
import OBR from '@owlbear-rodeo/sdk'
import GmScreen from './GmScreen'
import DiceContext from './DiceContext'
import CharacterPicker from './CharacterPicker'
import PersistenceContext, { usePersistenceContext } from './PersistenceContext'
import FeatsContext from './FeatsContext'
import Messages from './Messages'
import MessagesContext, { useMessagesCtx } from './MessagesContext'

export const theme = createTheme({
  typography: {
    fontFamily: [
      'Josefin Sans',
      '-apple-system',
      'BlinkMacSystemFont',
      'Segoe UI',
      'Roboto',
      'Oxygen',
      'Ubuntu',
      'Cantarell',
      'Fira Sans',
      'Droid Sans',
      'Helvetica Neue',
      'sans-serif',
    ].join(',')
  }
})

const TAB_MENU = 0
const TAB_CHARACTER_MAIN = 1
const TAB_GM_SCREEN = 3

function App() {
  return (
    <ThemeProvider theme={theme}>
      <PersistenceContext>
        <FeatsContext>
          <MessagesContext>
            <AppInner/>
          </MessagesContext>
        </FeatsContext>
      </PersistenceContext>
    </ThemeProvider>
  )
}

function AppInner() {
  const [email, setEmail] = useState('')
  const [tab, setTab] = useState(0)
  const [gameMaster, setGameMaster] = useState(window.location.search.includes('gm=true'))
  const { name, setName } = useMessagesCtx()

  useEffect(() => {
    setName(localStorage.getItem('name') || '')
    setEmail(localStorage.getItem('email') || '')
  })

  const handleEmail = (email: string) => {
    setEmail(email)
    localStorage.setItem('email', email)
  }

  const handleName = (name: string) => {
    setName(name)
    localStorage.setItem('name', name)
  }

  const gmCheck = async () => {
    console.log('Checking for GM role')
    const player = OBR.player
    const role = await player.getRole()

    if (role === 'GM') {
      setGameMaster(true)
      setTab(TAB_GM_SCREEN)
    } else {
      setGameMaster(false)
    }
  }
  useEffect(() => OBR.onReady(gmCheck), [])

  const { id, setId } = usePersistenceContext()

  const pickCharacter = (id: string) => {
    setId(id)
    setTab(TAB_CHARACTER_MAIN)
  }

  return (
    <div style={{ paddingLeft: 20, paddingRight: 20, paddingBottom: 40, height: '100%', background: '#E8E4D8', display: 'flex', flexDirection: 'column' }}>
      <DiceContext>
        <Stack direction="row" justifyContent="space-between" sx={{ width: 680 }}>
          <Tabs value={tab} onChange={(ev, newTab) => setTab(newTab)} >
            <Tab label="Pick Character" />
            <Tab label="Character" disabled={!id} />
            { gameMaster && <Tab label="GM Screen" /> }
          </Tabs>
          <Messages/>
        </Stack>
        { tab === TAB_MENU && (<CharacterPicker email={email} setEmail={handleEmail} name={name} setName={handleName} onSelect={pickCharacter}/>) }
        { tab === TAB_CHARACTER_MAIN && <Character /> }
        { tab === TAB_GM_SCREEN && <GmScreen /> }
      </DiceContext>
      <Typography variant='caption'>v0.6.12</Typography>
    </div>
  )
}

export default App
