export const WARLOCK = 'Warlock'
export const WITCH = 'Witch'
export const DESERT_RIDER = 'Desert Rider'
export const PIT_FIGHTER = 'Pit Fighter'
export const RAS_GODAI = 'Ras-Godai'
export const SEA_WOLF = 'Sea Wolf'
export const SEER = 'Seer'


